<template>
  <div>
    <b-card id="personMgmtCard" no-body>
      <b-row>
        <b-col cols="12" xl="1">
          {{ $t("EXPENSEITEM") }}
        </b-col>
        <b-col
          cols="12"
          xl="3"
          class="d-md-flex justify-content-between input-group"
        >
          <span>
            <button class="btn-sm btn-custom-color px-1 mr-2 d">
              {{ $t("REQUIREDBUTTON") }}
            </button>
          </span>
          <b-form-input
            v-model="selectedExpenseItem.expense_item_nm"
            placeholder=""
            class="mr-2"
            readonly
          ></b-form-input>

          <div>
            <b-button
              @click.prevent="addExpenseItem"
              :disabled="forUpdate"
              variant="primary"
              size="sm"
              >{{ $t("EXPENSEITEMSELECTION") }}</b-button
            >
          </div>
        </b-col>
        <b-col cols="12" xl="4" class="buttonNote mt-2">
          <span class="mr-2 font-weight-bold"
            >{{ $t("USERMANAGEMENT.SHOW") }} /
            {{ $t("USERMANAGEMENT.HIDE") }}</span
          >
          <b-form-radio-group
            id="radio-group-1"
            v-model="selectedExpenseItem.del_flg"
            :options="displayOptions"
            :disabled="forUpdate == false"
            size="sm"
          ></b-form-radio-group>
        </b-col>
        <b-col cols="12" xl="4">
          <span class="importantNote">{{ $t("USERMANAGEMENTNOTE") }}</span>
        </b-col>
      </b-row>
      <b-row class="mb-4">
        <b-col cols="12" xl="1"> </b-col>
        <b-col cols="12" xl="9">
          <show-errors
            :control="$v.selectedExpenseItem.expense_item_nm"
            :field="$t('EXPENSEITEM')"
          />
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="1" class="pr-0"> {{ $t("SUBJECT") }} </b-col>
        <b-col
          cols="12"
          xl="8"
          class="d-md-flex justify-content-between input-group"
        >
          <span>
            <button class="btn-sm float-left btn-custom-color px-1 mr-2">
              {{ $t("REQUIREDBUTTON") }}
            </button>
          </span>
          <b-form-input
            v-model="selectedExpenseItem.userManagementTitle"
            :required="true"
            placeholder=""
            class="mr-2"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="mb-4">
        <b-col cols="12" xl="1"></b-col>
        <b-col cols="12" xl="9">
          <show-errors
            :control="$v.selectedExpenseItem.userManagementTitle"
            :field="$t('SUBJECT')"
          />
        </b-col>
      </b-row>
      <b-row id="ckeditorUserManagement">
        <b-col cols="12" xl="1"> {{ $t("MAILCONTENT") }} </b-col>
        <b-col
          cols="12"
          xl="8"
          class="d-flex justify-content-start input-group"
        >
          <div>
            <button class="btn-sm float-left btn-custom-color px-1 mr-2">
              {{ $t("REQUIREDBUTTON") }}
            </button>
          </div>
          <!-- <b-form-textarea
            id="textarea-auto-height"
            v-model="selectedExpenseItem.userManagement"
          ></b-form-textarea> -->
          <ckeditor
            :editor="editor"
            v-model="selectedExpenseItem.userManagement"
            :config="editorConfig"
          ></ckeditor>
        </b-col>
      </b-row>
      <b-row class="mb-4">
        <b-col cols="12" xl="1"></b-col>
        <b-col cols="12" xl="9">
          <show-errors
            :control="$v.selectedExpenseItem.userManagement"
            :field="$t('MAILCONTENT')"
          />
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="11" class="ml-auto">
          <b-button
            @click.prevent="updateUserManagement"
            v-if="forUpdate"
            variant="primary"
            >{{ $t("UPDATE") }}</b-button
          >
          <b-button
            @click.prevent="createUserManagement"
            v-else
            variant="primary"
            >{{ $t("SAVE") }}</b-button
          >
        </b-col>
      </b-row>
    </b-card>
    <b-row>
      <b-modal
        id="tree-modal"
        ref="tree-modal"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="md"
        footer-class="border-0"
        header-class="border-0 bg-white pd-0"
        button-size="sm"
        hide-header-close
      >
        <template v-slot:modal-header="{ cancel }">
          <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
            {{ $t("ADDEXPENSEITEM") }}
          </div>
          <b-button
            @click="cancel"
            class="back-btn close-btn mt-2"
            variant="primary"
            >{{ $t("CLOSE") }}</b-button
          >
        </template>
        <b-card>
          <b-row>
            <b-col>
              <SubMenuTree
                @update-expense-type="updateExpenseItem"
                :tree-options="treeOptions"
                :treeTitle="addExpenseTitle"
              />
            </b-col>
          </b-row>
        </b-card>
        <b-row>
          <b-col cols="12" class="text-center mb-2">
            <b-button @click="hideModal" variant="primary" class="float-right"
              >{{ $t("ADD") }}
              <!-- <i v-if="selectLoader" class="fa fa-spinner fa-spin" /> -->
            </b-button>
          </b-col>
        </b-row>
      </b-modal>
    </b-row>
  </div>
</template>

<script>
import api from "@/shared/api";
import { mapGetters } from "vuex";
import { Settings } from "luxon";
import { required } from "vuelidate/lib/validators";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import SubMenuTree from "@/components/SubMenuTree.vue";
import ShowErrors from "@/components/ShowErrors";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
// import CKEditor from "@ckeditor/ckeditor5-vue";
import { endPoint } from "@/shared/endPoint";

Settings.defaultLocale = "ja";

export default {
  name: "AddEdit",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      forUpdate: false,
      addExpenseTitle: "",
      editData: {},
      user_management_no: "",
      allTreeData: [],
      selectedTreeItem: [],

      displayOptions: [
        { value: "0", text: "表示する" },
        { value: "1", text: "非表示" },
      ],

      selectedExpenseItem: {
        expense_item_nm: "",
        expense_item_cd: "",
        accounts_name_cd: "",
        userManagementTitle: "",
        userManagement: "",
        del_flg: "0",
      },
      expenseItemOptions: [],
      treeOptions: {
        checkbox: false,
      },
      tabIndex: 0,
      fileName: "",
      editor: ClassicEditor,
      editorConfig: {
        language: Settings.defaultLocale,
        toolbar: {
          items: [
            "heading",
            "|",
            "bold",
            "italic",
            "bulletedList",
            "numberedList",
            "|",
            "indent",
            "outdent",
            "|",
            "blockQuote",
            "insertTable",
            "mediaEmbed",
            "undo",
            "redo",
          ],
        },
        table: {
          contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
        },
      },
    };
  },
  validations: {
    selectedExpenseItem: {
      expense_item_nm: {
        required,
      },
      userManagementTitle: {
        required,
      },
      userManagement: {
        required,
      },
    },
  },
  computed: {
    ...mapGetters({
      selected_expense_item: "getSelectedExpenseItem",
      userDetails: "getUserDetails",
    }),
  },

  // watch: {
  //   "selectedExpenseItem.userManagement": function (newVal, old) {
  //     this.selectedExpenseItem.userManagement = newVal;
  //   },
  // },
  watch: {
    userManagement: function (val) {
      this.$emit("input", val);
    },
  },
  created() {
    this.getTreeData();
  },
  mounted() {
    console.log(this.displayOptions);
    if (this.$route.params.item) {
      this.editData = this.$route.params.item;
      this.forUpdate =
        this.editData.forUpdate != undefined ? this.editData.forUpdate : false;
      // this.selectedExpenseItem.userManagementTitle = this.editData.user_management_title;
      // this.selectedExpenseItem.userManagement = this.editData.user_management;
      this.user_management_no = this.editData.user_management_no;
      setTimeout(() => {
        this.selectedExpenseItem = {
          client_no: this.userDetails.client_no,
          expense_item_cd: this.editData.expense_item_cd,
          expense_item_nm: this.editData.expense_item_nm,
          accounts_name_cd: this.editData.accounts_name_cd,
          userManagementTitle: this.editData.user_management_title,
          userManagement: this.editData.user_management,
          del_flg: this.editData.del_flg === "非表示" ? "1" : "0",
        };
        this.$store.dispatch("updateSelectedExpenseItem", {
          accounts_name_cd: this.editData.accounts_name_cd,
          expense_item_cd: this.editData.expense_item_cd,
          expense_item_nm: this.editData.expense_item_nm,
        });
      }, 0);
    }
    this.resetTreeList();
    this.getExpenseItemList();
  },
  methods: {
    status(validation) {
      return {
        error: validation.$error,
        dirty: validation.$dirty,
      };
    },
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          limit: 5000,
          fieldlist: [
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_accounts_name.use_flg",
              value: true,
            },
          ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );

      if (data.success) {
        let responseData = data.data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
      }
    },

    hideModal() {
      this.$refs["tree-modal"].hide();
    },

    resetTreeList() {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: "",
        expense_item_cd: "",
        expense_item_nm: "",
      });
    },

    addExpenseItem() {
      console.log("ppp", this.selectedExpenseItem.del_flg);
      this.$refs["tree-modal"].show();
    },
    updateExpenseItem(item) {
      console.log("Check", item);
      Object.assign(item, { del_flg: this.selectedExpenseItem.del_flg });
      this.selectedExpenseItem = item;

      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
      this.selectedExpenseItem = treeItem;
      this.selectedExpenseItem.del_flg = "0";
      console.log("Check1", this.selectedExpenseItem);

      //console.log("TREE ITEM SELECTED", this.selectedTreeItem);
    },
    async getExpenseItemList() {
      const { data } = await BasicInfoApis.postAPI(endPoint.READMEXPENSEITEM, {
        fieldlist: ["accounts_name_cd", "expense_item_cd", "expense_item_nm"],
      });
      if (data.success) {
        let responseData = data.data;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
              },
            };
            expenseListData.push(iterateExpenseItem);
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    async createUserManagement() {
      this.$v.selectedExpenseItem.$touch();
      if (!this.$v.selectedExpenseItem.$invalid) {
        let createdUserManagement = await api.post(
          endPoint.MASTERCREATEDATAMUSERMANAGEMENT,
          {
            user_management_title: this.selectedExpenseItem.userManagementTitle,
            user_management: this.selectedExpenseItem.userManagement,
            del_flg: this.selectedExpenseItem.del_flg == 0 ? false : true,
            accounts_name_cd: this.selectedExpenseItem.accounts_name_cd,
            expense_item_cd: this.selectedExpenseItem.expense_item_cd,
            update_id: localStorage.getItem("personID"),
            create_id: localStorage.getItem("personID"),
          }
        );

        if (createdUserManagement.data.success) {
          // this.$swal({
          //   type: "success",
          //   text: this.$t("CREATED"),
          //   timer: 2000,
          // });

          this.redirectTOPrev();
        } else {
          this.$swal({
            type: "warning",
            text: this.$t("NO") + " " + this.$t("CREATED"),
            timer: 2000,
          });
        }
      }
    },
    redirectTOPrev() {
      this.$router.push({ name: "user_management" });
    },
    async updateUserManagement() {
      this.$v.selectedExpenseItem.$touch();
      if (!this.$v.selectedExpenseItem.$invalid) {
        let updatedData = await api.post(endPoint.MASTERUPDATEMUSERMANAGEMENT, {
          data: [
            {
              user_management_title: this.selectedExpenseItem
                .userManagementTitle,
              user_management: this.selectedExpenseItem.userManagement,
              del_flg: this.selectedExpenseItem.del_flg,
              accounts_name_cd: this.selectedExpenseItem.accounts_name_cd,
              expense_item_cd: this.selectedExpenseItem.expense_item_cd,
              update_id: localStorage.getItem("personID"),
              create_id: localStorage.getItem("personID"),
            },
          ],
          conditions: [
            { key: "user_management_no", value: this.user_management_no },
          ],
        });
        if (updatedData.data.success) {
          this.redirectTOPrev();
        } else {
          this.$swal({
            type: "warning",
            text: this.$t("NO") + " " + this.$t("CREATED"),
            timer: 2000,
          });
        }
      }
    },
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-black"];
      } else {
        return ["bg-primary", "text-black"];
      }
    },
  },
};
</script>
<style lang="scss" scoped>
#textarea-auto-height {
  min-height: 200px;
}
#personMgmtCard.card {
  border: none !important;
}

$link-color: #6790b9;
#personMgmtCard .nav-tabs .nav-item {
  width: 10rem !important;
  margin-right: 3px;
  text-decoration: underline;
}
#personMgmtCard .nav-tabs .nav-link {
  border-radius: 6px !important;
  border: 1px solid #c8ced3 !important;
}
#personMgmtCard .nav-link {
  padding: 0.25rem 1rem !important;
  text-align: center;
  color: #fff;
}
#personMgmtCard .nav-tabs .nav-link.active {
  color: $link-color;
  background: #fff !important;
}

.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}
.buttonNote {
  display: inline-flex;
  justify-content: flex-end;
}
.importantNote {
  color: red;
  font-size: 12px;
}

.btn-custom-color:hover {
  color: white;
}
</style>
<style>
#ckeditorUserManagement .ck.ck-editor {
  width: 93.4% !important;
}

#ckeditorUserManagement .ck-content {
  min-height: 200px;
}

@media only screen and (max-width: 500px) {
  #ckeditorUserManagement .ck.ck-reset_all,
  .ck.ck-reset_all * {
    font-size: 0.6rem !important;
  }
}
</style>